<template>
  <div id="info">
    <div style="width: 100% ;height:44px;">
      <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-top:20px;">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>区块链基本信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div style="height:800px; border:1px solid gary;display:flex;flex-direction:column;" class="three">
      <div class="top-con">
        <div class="left-con">
          <div class="row">
            <span style="margin-right:20px;">区块链服务名称:</span>
            <span>{{name}}</span>
          </div>
          <div class="row">
            <span style="margin-right:20px;">版本信息:</span>
            <span>{{version}}</span>
          </div>
          <div class="row">
            <span style="margin-right:20px;">简介:</span>
            <span>{{descri}}</span>
          </div>
        </div>
        <div class="right-con">
          <div class="row">
            <span style="margin-right:20px;">共识策略:</span>
            <span>{{gscl}}</span>
          </div>
          <div class="row">
            <span style="margin-right:20px;">创建时间:</span>
            <span>{{createTime}}</span>
          </div>
        </div>
      </div>
      <div class="table-con">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="name" label="组织" width="180"></el-table-column>
          <el-table-column prop="type" label="节点类型" width="180"></el-table-column>
          <el-table-column prop="domain_name" label="节点域名"></el-table-column>
          <el-table-column prop="port" label="端口"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "info",
  data() {
    return {
      name: "区块链服务名称",
      descri: "区块链服务名称",
      gscl: "区块链服务名称",
      createTime: "区块链服务名称",
      version: "区块链服务名称",
      tableData: []
    };
  },

  created() {
    this.$axios.get("http://127.0.0.1:8080/api/bc/network").then(res => {
      if (res.data.return_code === 200) {
        let data = res.data.data;
        this.name = data.name;
        this.descri = data.introduction;
        this.createTime = data.create_date;
        this.version = data.version;
        this.tableData = data.orgs;
      }
    });
  },
  components: {}
};
</script>


<style lang="less" scoped>
.table-con {
  margin-left: 150px;
  margin-right: 150px;
  flex:1 ;
}
.top-con {
  padding: 50px;
  width: 100%;
  height: 180px;
  display: flex;
  margin-bottom: 40px;
}
.left-con {
  width: 35%;
  height: 100%;
}
.right-con {
  width: 40%;
  height: 100%;
}
.row {
  width: 100%;
  height: 60px;
  line-height: 60px;
}

.three {
  -moz-box-shadow: 0 0 10px #06c;
  -webkit-box-shadow: 0 0 10px #06c;
  box-shadow: 0 0 10px #06c;
}
</style>
